<script>
  export default {
    data(){
      return{
        show:true,
        // 
        key:"",
        showModal:false,
        // 
        uname:"",                                         
        upwd:"",
        num:1,
        islogin:false
      }
    },
    // 
    directives:{
      focus:{
        inserted:function(el){
          el.focus();
        }
      }
    },
    created() {
      this.key=this.$route.params.key;
    },
    methods:{
      close(){
        this.show=false;
      },
      // 
      search(){
        var key=this.key;
        if(!key){
          this.alert("!");
          return;
        }
        if(this.key.trim()!==""){
          this.$router.push(`/products/${this.key}`);
        }
        if(this.$route.path.split("/")[1]=="products"){
          location.reload();
        }
      },
      loginShow(){
        this.showModal=!this.showModal;
      },
      loginClose(){
        this.showModal=!this.showModal;
      },
      login(){
        var uname=this.uname;
        var upwd=this.upwd;
        this.axios.get("login",{
          params:{
            uname,
            upwd,
          }
        }).then(result=>{
          if(result.data.code==1){
            this.loginClose();
            this.islogin=true;
            setTimeout(() => {
              location.href="#/index";
            },900);
            sessionStorage.setItem("uname",this.uname);
            sessionStorage.setItem("num",this.num);
          }else{
            alert("!!");
          }
        })
      },
      exit(){
        alert("")
        sessionStorage.removeItem("uname");
        this.islogin=false;
      }
    },
    watch: {
      kwords(){
        this.search();
      }
    },
  }
</script>
