          vm.isActive=true;
        }else{
          vm.isActive=false;
        }
      }
    },
    methods:{
      bannerList(){
        this.axios.get("bannerlist").then(result=>{
          this.list=result.data.data.splice(0,8);
        })
      },
      bannerList1(){
        this.axios.get("bannerList").then(result=>{
          this.list1=result.data.data.splice(8,15);
        })
      },
      //class
      tabHide() {
        this.tabcontentShow = false;
      },
      tabChange(i) {
        this.tabcontentShow = true;
        this.isActive = i;
      },
      startMove () {
        // eslint-disable-next-line
        var timer = setTimeout(() => {
          if (this.number === 4) {
            this.number = 0;
          } else {
            this.number += 1;
          }
          this.startMove();
        }, 2000); // 2000
      },
      // 
      prev(){
        // 
        if(this.nextDisabled==false){
          this.moved++;
          this.ulScrollStyle["margin-left"]=this.moved*-1200+"px";
        } 
      },
      next(){
        // 
        if(this.prevDisabled==false){
          this.moved--;
          this.ulScrollStyle["margin-left"]=this.moved*-1200+"px";
        }
      },
      // 
      toTop(i){
        // i
        //console.log(i);
      document.documentElement.scrollTop-=i;
       if(document.documentElement.scrollTop>0){
         var t=setTimeout(()=>this.toTop(i),10);
       }else{
         clearTimeout(t);
       }
      },
      // 
      showDetails(num){
        var el=document.getElementsByClassName(`listshow${num}`)[0];
        console.log(el.offsetTop);
        if(this.showIndex==num){
          this.showIndex=null;
        }else{
          this.showIndex=num;
        }
        this.$nextTick(function(){
          window.scrollTo({"behavior":"smooth","top":el.offsetTop});
        })
      },
      // 
      msgShow(){
        this.showModal=!this.showModal;
      },
      msgClose(){
        this.showModal=!this.showModal;
      },
      submit(){
        var title=this.title;
        var content=this.content;
        var msg=this.msg;
        this.axios.get("msg",{
          params:{
            title,content,msg
          }
        }).then(result=>{
          if(result.data.code==1){
            this.msgClose();
          }else{
            alert(""+result.data.msg);
          }
        })
      }
    },
    //2.header.vue
    components:{  
      myHeader,
      myFooter,
    } 
  }
</script>
