<template>
  <div>
    <header class="d-flex justify-content-around">
      <img src="imgs/reg-logo.png">
      <h4 class="mt-2"></h4>
    </header>
    <section>
      <ul class="nav nav-pills">
        <li class="nav-item">
          <a href="#pill1" data-toggle="pill" class="nav-link active">
          <span class="iconfont icon-phone-o"></span>
          
          </a>
        </li>
        <li class="nav-item">
          <a href="#pill2" data-toggle="pill" class="nav-link">
          <span class="iconfont icon-mail"></span>
          
          </a>
        </li>
        <li class="mt-2"><router-link to="login"></router-link></li>
      </ul>
      <!--  -->
      <div class="tab-content">
        <div id="pill1" class="tab-pane active">
          <form action="#" method="get">
            <div class="text d-flex">
              <div class="text-left text-center"></div>
              <div class="text-right">            
                <input type="text" class="input" placeholder="" v-model="uname">
              </div>
            </div>
            <div class="text d-flex">
              <div class="text-left text-center">/</div>
              <div class="text-right">     
                <select v-model="city">
                  <option v-for="item of option1" :key="item.i" >{{item.text}}</option>
                </select>       
                <input type="text" class="input" :value="option1.value">
                <span class="iconfont icon-xiaosanjiao-"></span>
              </div>
            </div>
            <div class="text d-flex">
              <div class="text-left text-center">
                <select >
                  <option v-for="item of option2" :key="item.i">{{item.text}}</option>
                </select>       
                <input type="text" class="input" :value="option2.value">
                <span class="iconfont icon-xiaosanjiao-"></span>
              </div>
              <div class="text-right">     
                <input type="text" placeholder="" class="input" v-model="phone">
              </div>
            </div>
            <div class="text d-flex">
              <div class="text-left text-center">
                <span></span>
              </div>
              <div class="text-right">     
                <input type="password" placeholder="" class="input" v-model="upwd">
              </div>
            </div>
            <div class="text d-flex">
              <div class="text-left text-center">
                <span></span>
              </div>
              <div class="text-right">     
                <input type="password" placeholder="" class="input" v-model="upwd2">
              </div>
            </div>
          </form>
            <div class="text d-flex">
              <div class="text-left text-center">
                <span></span>
              </div>
              <div class="text-right">     
                <input type="text" placeholder="" class="input" v-model="codevalue">
              </div>
              <div class="code">
                <my-text :identifyCode="identifyCode"></my-text>
                <span @click="refreshCode"></span>
              </div>
            </div>
            <input type="submit" value="" @click="submit">
        </div>
        <div id="pill2" class="tab-pane">
          <form action="#" method="get">
            <div class="text d-flex">
              <div class="text-left text-center"></div>
              <div class="text-right">            
                <input type="text" class="input" placeholder="" v-model="uname">
              </div>
            </div>
            <div class="text d-flex">
              <div class="text-left text-center">/</div>
              <div class="text-right">     
                <select v-model="city">
                  <option v-for="item of option1" :key="item.i" >{{item.text}}</option>
                </select>       
                <input type="text" class="input" :value="option1.value">
                <span class="iconfont icon-xiaosanjiao-"></span>
              </div>
            </div>
            <div class="text d-flex">
              <div class="text-left text-center">
                <select >
                  <option v-for="item of option2" :key="item.i">{{item.text}}</option>
                </select>       
                <input type="text" class="input" :value="option2.value">
                <span class="iconfont icon-xiaosanjiao-"></span>
              </div>
              <div class="text-right">     
                <input type="text" placeholder="" class="input" v-model="phone">
              </div>
            </div>
            <div class="text d-flex">
              <div class="text-left text-center"></div>
              <div class="text-right">     
                <input type="text" placeholder="" class="input" v-model="email">
              </div>
            </div>  
            <div class="text d-flex">
              <div class="text-left text-center">
                <span></span>
              </div>
              <div class="text-right">     
                <input type="password" placeholder="" class="input" v-model="upwd">
              </div>
            </div>
            <div class="text d-flex">
              <div class="text-left text-center">
                <span></span>
              </div>
              <div class="text-right">     
                <input type="password" placeholder="" class="input" v-model="upwd2">
              </div>
            </div>
          </form>
          <div class="text d-flex">
            <div class="text-left text-center">
              <span></span>
            </div>
            <div class="text-right">     
              <input type="text" placeholder="" class="input" v-model="codevalue">
            </div>
            <div class="code">
              <my-text1 :identifyCode="identifyCode"></my-text1>
              <span @click="refreshCode"></span>
            </div>
          </div>
          <input type="submit" value="" @click="submit1">
        </div>
      </div>
      <div class="border-bottom mt-5"></div>
    </section>
  </div>
</template>
<script>
import myText from "@/components/text.vue"
import myText1 from "@/components/text1.vue"
export default {
  data(){
    return{
      option1:[
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
        {text:"",value:""},
      ],
      option2:[
        {text:"+93()",value:"+93()"},
        {text:"+20()",value:"+20()"},
        {text:"+61()",value:"+61()"},
        {text:"+55()",value:"+55()"},
        {text:"+32()",value:"+32()"},
        {text:"+49()",value:"+49()"},
        {text:"+7()",value:"+7()"},
        {text:"+33()",value:"+33()"},
        {text:"+81()",value:"+81()"},
        {text:"+86()",value:"+86()"},
        {text:"+853()",value:"+853()"},
        {text:"+886()",value:"+886()"},
        {text:"+852()",value:"+852()"},
      ],
      // 
      identifyCodes: "1234567890",
      identifyCode: "",
      codevalue:"",
      //
      uname:"",
      phone:"",
      upwd:"",
      upwd2:"",
      city:"",
      email:"",
    }
  },
  created() {

  },
   mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes,4);
  },
  methods: {
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },
    refreshCode() { 
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },   
    makeCode(o,l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
          this.randomNum(0, this.identifyCodes.length)
        ];
      }
    },
    submit(){
      // 
      if(this.codevalue==this.identifyCode){
        var uname=this.uname;
        var phone=this.phone;
        var upwd=this.upwd;
        var upwd2=this.upwd;
        var city=this.city;
        this.axios.get("reg",{
          params:{
            uname,phone,upwd,upwd2,city
          }
        }).then(result=>{
          if(result.data.code==1){
            setTimeout(() => {
              location.href="#/login";
            },900);
          }
        })
      }else{
        alert("");
      }
    },
    submit1(){
      if(this.codevalue==this.identifyCode){
        var uname=this.uname;
        var phone=this.phone;
        var upwd=this.upwd;
        var upwd2=this.upwd;
        var city=this.city;
        var email=this.email;
        this.axios.get("reg",{
          params:{
            uname,phone,upwd,upwd2,city,email
          }
        }).then(result=>{
          console.log(result);
        })
      }else{
        alert("");
      }
    }
  },
  components:{  
    myText,
    myText1,
  } 
}
</script>
<style scoped>
  header{
    background-color:#000;
    padding:15px 0;
    color:#fff;
  }
  section{
    width:960px;
    height:620px;
    margin:60px auto;
  }
  section .tab-content{
    width:960px;
    height:598px;
    background-color:#fff;
    padding:20px 270px;
  }
  section .tab-content .text{
    width:420px;
    height:44px;
    background:#f7f7f7;
    border-radius:25px;
    line-height:44px;
    border:1px solid #ddd;
    font-size:14px;
    margin-top:20px;
    position:relative;
  }
.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  color: #fff;
  background-color: #a51b1b;
  border-radius:0px;
}
  .tab-pane .text>.text-left{
    width:123px;
    border-right:1px solid #ddd;
  }
  .text-right{
    width:250px;
  }
  .text .input{
    width:260px;
    border:0;
    background-color:transparent;
  }
  .text .text-right{
    position: relative;
  }
  select{
    background-color:transparent;
  }
  .text-right span.iconfont{
    font-size:20px;
    position:absolute;
    top:0;
  } 
  .text-left span.iconfont{
    font-size:20px;
    position:absolute;
    top:0;
    left:90px;
  } 
.code {
  margin: 400px auto;
  width: 114px;
  height: 40px;
  border: 1px solid red;
  position:absolute;
  top:-400px;
  right:-120px;
}
  input[type='submit']{
    background-color:#a51b1b;
    border:0;
    width:328px;
    height:44px;
    border-radius:30px;
    margin-top:50px;
  }
  input[type=text]{
    font-size:14px;
  }
  input[type=password]{
    font-size:14px;
  }
</style>


